<template>
  <div class="food-login">
    <img class="login-bg" src="../../assets/img/food-login-bg.jpg" alt>
    <img class="login-img" src="../../assets/img/foodLogo.png" alt>
    <div class="input-container">
      <input type="text" placeholder="填写站点信息" v-model="siteInfo">
    </div>

    <div class="login-btn" @click="okFun()">确 定</div>
  </div>
</template>
<script>
import { Indicator, Toast } from "mint-ui";
export default {
  data() {
    return {
      siteInfo: ""
    };
  },
  mounted() {},
  methods: {
    okFun: function() {
      if (!this.siteInfo) {
        this.msg("请填写站点信息");
        return;
      }
      if (window.navigator.userAgent.indexOf("Cordova") > 0) {
        getSn(this.getSnSuccess);
      } else {
        this.sendData("111111111");
      }
    },
    getSnSuccess(v) {
      this.sendData(v);
    },
    sendData(sn) {
      this.$http
        .get("/ohotel/router", {
          params: {
            method: "saveWorkStation",
            stationDesc: this.siteInfo,
            sinNo: sn
          },
          vv: 5
        })
        .then(res => {
          console.log("denglu:", res);
          if (res.data.code == "0") {
            let foodUserInfo = JSON.parse(localStorage.foodUserInfo);
            foodUserInfo.workStationCode = res.data.data.workStationCode;
            localStorage.foodUserInfo = JSON.stringify(foodUserInfo);
            this.$router.push("/foodIndex");
          } else {
            this.msg(res.data.msg);
          }
        });
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    }
  }
};
</script>
<style lang="less" scoped>
.food-login {
  position: relative;
  .login-bg {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }
  .login-img {
    width: 106px;
    margin-top: 92px;
    margin-bottom: 1.067rem;
  }
  .input-container {
    position: relative;
    margin: 0 1.2rem;
    border-bottom: 1px solid #ffffff;
    padding: 0.133rem 0;
    margin-bottom: 161px;

    input {
      width: 100%;
      border: none;
      background: none;
      outline: none;
      color: #fff;
      font-size: 0.427rem;
    }
    input::-webkit-input-placeholder {
      color: #fff;
    }
  }
  .login-btn {
    margin: 0 1.2rem;
    margin-top: 0.933rem;
    margin-bottom: 2.4rem;
    font-size: 0.48rem;
    color: #ffffff;
    height: 44px;
    line-height: 44px;
    background-image: linear-gradient(-90deg, #f6a129 0%, #ff6e61 98%);
    border-radius: 2.667rem;
  }
}
</style>




